<template>
  <div>
    <loading-component :value="loading" />
    <h1>agent_id: {{agent_id}}</h1>

    <ul two-line>
        <li v-for="(survey, index) in surveys" :key="index">
            <p class="header to-uppercase" v-text="survey.title"></p>
        <v-divider v-if="index < surveys.length - 1" :key="index"></v-divider>
        </li>
    </ul>
  </div>
</template>
<script>
import LoadingComponent from "@/components/LoadingComponent";

export default {
  name: "surveys",
  components: {LoadingComponent},
  data() {
    return {
      surveys: [],
      agent_id: this.$store.state.agent_id,
      loading: false
    };
  },
  mounted() {
    this.loading = true;
    this.axios.get(this.api.ACTIONS.SURVEY).then((res) => {
      this.surveys = res.data.data.data;
      this.loading = false;
    });
  },
};
</script>
